.offcanvas-header {
  box-shadow: 0 0 17px rgba(0, 0, 0, 0.1);
}
.offcanvas-title {
  font-size: 16px;
  text-transform: capitalize;
  font-weight: 700;
}
.offcanvas-title svg {
  margin-right: 12px;
  color: var(--main-color);
}

.offcanvas-cart .offcanvas-footer {
  padding: 24px;
  color: #94A3B8;
  font-weight: 400;
}
.offcanvas-cart .offcanvas-footer p {
  margin-bottom: 10px;
  display: flex;
  justify-content: space-between;
}
.offcanvas-cart .offcanvas-footer p.total {
  color: #000;
  border-top: 1px solid #ddd;
  padding-top: 12px;
  margin-top: 16px;
  font-size: 16px;
  font-weight: 600;
}
.offcanvas-cart .cart-item {
  position: relative;
  display: flex;
  padding: 24px 16px;
  border: 1px dashed transparent;
  border-bottom: 1px solid #E2E8F0;
  background-color: transparent;
}
.offcanvas-cart .cart-item:hover {
  border-color: var(--dark-color);
}
.offcanvas-cart .cart-item:nth-child(odd) {
  background-color: #F8FAFC;
}
.offcanvas-cart .cart-item .item-image {
  flex-shrink: 0;
}
.offcanvas-cart .cart-item .item-image img {
  width: 80px;
  height: 60px;
  border-radius: 4px;
  object-fit: cover;
}
.offcanvas-cart .cart-item .item-cyan {
  flex-grow: 1;
  padding: 0 16px;
}
.offcanvas-cart .cart-item .item-cyan .title {
  font-size: 13px;
  text-transform: capitalize;
  font-weight: 600;
  line-height: 22px;
}
.offcanvas-cart .cart-item .price-amount {
  display: flex;
  align-items: center;
  margin-top: 4px;
}
.offcanvas-cart .cart-item .price-amount .price {
  font-size: 18px;
  font-weight: 700;
  margin-right: 4px;
}
@media (max-width: 992px) {
  .offcanvas-cart .cart-item .price-amount {
    display: block;
  }
  .offcanvas-cart .cart-item .price-amount .price {
    margin-bottom: 4px;
  }
}
.offcanvas-cart .cart-item .quantity-input {
  border: 1px solid #e5e7eb;
  border-radius: 50px;
  display: flex;
  margin-left: auto;
  height: 32px;
  background-color: white;
  transition: border-color 0.2s;
}
.offcanvas-cart .cart-item .quantity-input:hover {
  border-color: var(--main-color);
}
.offcanvas-cart .cart-item .quantity-input input {
  width: 44px;
  border: none;
  text-align: center;
  outline: none;
  background-color: transparent;
}
.offcanvas-cart .cart-item .quantity-input button {
  width: 32px;
}
.offcanvas-cart .cart-item .item-remove-btn {
  position: absolute;
  top: 10px;
  right: 10px;
  background-color: #000;
  width: 18px;
  height: 18px;
  padding: 0;
  border-radius: 4px;
  display: grid;
  place-content: center;
  transition: background-color 0.2s;
}
.offcanvas-cart .cart-item .item-remove-btn:hover {
  background-color: var(--red-color);
}
.offcanvas-cart .cart-item .item-remove-btn svg {
  color: #fff;
  width: 15px;
  height: 15px;
}
.offcanvas-cart .cart-item:hover .item-remove-btn {
  display: inline-grid;
}

.contact-info-canvas h6 {
  font-size: 14px;
  font-weight: 600;
  text-transform: uppercase;
  margin-bottom: 16px;
}
.contact-info-canvas .socials svg {
  color: var(--dark-color);
}
.contact-info-canvas .subscribe form .form-control {
  font-size: 13px;
  line-height: 32px;
  border-radius: 4px;
  margin-bottom: 12px;
}
.contact-info-canvas .contact-info .icon svg {
  color: var(--main-color);
  margin-right: 16px;
}

.form-control {
  border-radius: 0;
  font-size: inherit;
}
.form-control:focus {
  outline: none;
  border-color: var(--main-color);
  box-shadow: none;
}

.dropdown-menu {
  font-size: inherit;
  border: none;
  border-radius: 0.25rem;
  box-shadow: 0 0 1.0625rem rgba(0, 0, 0, 0.1);
}

@media (max-width: 1366px) {
  .container {
    max-width: 1320px;
  }
}
@media (max-width: 992px) {
  .container {
    max-width: 960px;
  }
}
@media (max-width: 768px) {
  .container {
    max-width: 100%;
    padding-left: 15px;
    padding-right: 15px;
  }
}
.header .search .search-category button, .header .search {
  display: flex;
  align-items: center;
}

.app-header::before {
  position: absolute;
  content: "";
  width: 100%;
  height: 100%;
}

.app-header {
  position: relative;
}
.app-header::before {
  background: linear-gradient(to bottom, rgba(var(--main-rgb-color), 0.2) 0%, transparent 79%);
  z-index: -1;
}
@media (max-width: 992px) {
  .app-header {
    height: 100vh;
  }
}

.header {
  padding: 18px 0;
}
.header .logo a {
  font-weight: 700;
  font-size: 24px;
  letter-spacing: 0.5px;
}
.header .logo svg {
  width: 40px;
  height: 32px;
  color: var(--main-color);
}
@media (max-width: 992px) {
  .header .logo svg {
    width: 24px;
    height: 24px;
    color: var(--main-color);
    margin-right: 4px;
  }
  .header .logo a {
    font-size: 19px;
    letter-spacing: 0.5px;
    font-weight: 600;
    text-transform: uppercase;
    display: inline-flex;
    align-items: center;
  }
}
.header .search {
  padding: 10px 16px;
  background-color: #fff;
  border-radius: var(--border-radius-size);
}
.header .search .search-category {
  width: 180px;
  display: flex;
  align-items: center;
}
.header .search .search-category svg:first-of-type {
  width: 22px;
  margin-right: 12px;
}
.header .search .search-category button {
  border: none;
  margin-right: 4px;
  position: relative;
}
.header .search .search-category button::after {
  content: "";
  right: 0;
  top: 50%;
  width: 1px;
  height: 20px;
  position: absolute;
  transform: translateY(-50%);
  background-color: rgba(0, 0, 0, 0.1);
}
.header .search .search-category .dropdown-item.blue svg {
  color: #2563EB;
}
.header .search .search-category .dropdown-item.amber svg {
