}
.header .search .search-category .dropdown-item.violet svg {
  color: #8B5CF6;
}
.header .search .search-category .dropdown-item.purple svg {
  color: #A855F7;
}
.header .search .search-category .dropdown-item.rose svg {
  color: #F43F5E;
}
.header .search .search-category .dropdown-item.orange svg {
  color: #F97316;
}
.header .search .search-category .dropdown-menu {
  width: 200px;
}
.header .social-links {
  align-self: center;
}
.header .social-links .nav {
  justify-content: flex-end;
}
.header .social-links a {
  padding: 0 8px;
}
.header .social-links svg {
  width: 20px;
  height: auto;
  color: var(--dark-color, #000);
}
@media (max-width: 1200px) {
  .header .social-links {
    display: none;
  }
}
.header .navbar-toggler {
  width: 44px;
}
.header button {
  padding: 0 6px;
  position: relative;
}
.header .shopping-cart span {
  position: absolute;
  width: 14px;
  height: 14px;
  border-radius: 50%;
  font-size: 10px;
  color: #fff;
  background-color: #E91E63;
  right: 7px;
  top: 6px;
}
@media (max-width: 992px) {
  .header .shopping-cart span {
    right: 0;
    top: -3px;
  }
}
@media (max-width: 992px) {
  .header .inner-wrap {
    border-radius: var(--border-radius-size);
    background-color: #fff;
    padding: 12px 8px;
    box-shadow: 0 0 15px rgba(0, 0, 0, 0.1);
  }
}

.navbar .offcanvas {
  background-color: #fff !important;
  padding: 14px 12px;
  border-radius: var(--border-radius-size);
}
@media (max-width: 992px) {
  .navbar .offcanvas {
    overflow: hidden;
    width: 300px;
    padding: 0;
  }
}
.navbar .offcanvas-header {
  border-bottom: 0.0625rem solid rgba(0, 0, 0, 0.1);
  padding: 0 24px;
  height: 47px;
}
@media (max-width: 992px) {
  .navbar .offcanvas-body {
    padding: 0;
  }
}
.navbar .dropdown-menu {
  opacity: 0;
  padding: 16px;
  display: block;
  visibility: hidden;
  transform: translateY(0.625rem);
  transition: opacity 0.3s ease-in-out, transform 0.3s ease-in-out;
}
.navbar .dropdown-menu .dropdown-menu {
  left: 100%;
  top: 0;
}
@media (max-width: 992px) {
  .navbar .dropdown-menu {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    padding: 0;
    background-color: #fff;
    box-shadow: none;
    transform: translateX(100%);
    transition: transform 0.3s ease-in-out;
    opacity: 1;
    visibility: visible;
  }
  .navbar .dropdown-menu.show {
    transform: translateX(0);
  }
}
.navbar .dropdown:hover > .dropdown-menu {
  opacity: 1;
  visibility: visible;
  transform: translateY(0);
}
.navbar .dropdown-item {
  display: flex;
  padding: 8px 16px;
  font-size: inherit;
  align-items: center;
  text-transform: capitalize;
  color: rgba(0, 0, 0, 0.8);
  justify-content: space-between;
  transition: background 0.3s ease, color 0.3s ease;
}
.navbar .dropdown-item.active, .navbar .dropdown-item.active:hover {
  color: #fff;
  background-color: var(--dark-color);
}
.navbar .dropdown-item:hover {
  color: var(--dark-color);
  background-color: #F1F5F9;
}
.navbar .dropdown-item.dropdown-toggle::after {
  transform: rotate(-90deg);
}
@media (max-width: 992px) {
  .navbar .dropdown-item {
    padding: 16px 24px 16px 42px;
    border-bottom: 0.0625rem solid rgba(0, 0, 0, 0.1);
    font-weight: 600;
  }
  .navbar .dropdown-item.active {
    position: relative;
    background-color: rgba(0, 0, 0, 0.03);
    color: var(--amber-color);
  }
  .navbar .dropdown-item.active::after {
    content: "✔";
    position: absolute;
    left: 16px;
    top: 50%;
    transform: translateY(-50%);
  }
  .navbar .dropdown-item.active:hover, .navbar .dropdown-item.active:active {
    background-color: transparent;
  }
}
.navbar .dropdown-toggle::after {
  border-width: 0.25rem;
  opacity: 0.7;
  margin-left: 8px;
}
@media (max-width: 992px) {
  .navbar .dropdown-toggle::after {
    display: inline-block;
    margin-left: 0.255em;
    content: "";
    border-top: 0.25rem solid;
    border-right: 0.25rem solid transparent;
    border-bottom: 0;
    border-left: 0.25rem solid transparent;
  }
}
.navbar .nav-item {
  cursor: pointer;
  position: relative;
}
.navbar .nav-item::after {
  content: "";
  position: absolute;
  height: 0.875rem;
  width: 0.0625rem;
  background: linear-gradient(45deg, #f2ea71, #c1ecc3);
  left: 0;
  top: 50%;
  transform: translateY(-50%);
}
.navbar .nav-item:first-of-type::after {
  display: none;
}
.navbar .nav-link {
  text-transform: uppercase;
  padding: 8px 16px !important;
}
@media (max-width: 992px) {
  .navbar .nav-link {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-bottom: 0.0625rem solid rgba(0, 0, 0, 0.1);
    padding: 16px 24px;
    color: #000;
    font-weight: 600;
  }
  .navbar .nav-link::after {
    transform: rotate(-90deg);
  }
}
.navbar .notify-count {
  top: 0.1875rem;
  left: 28px;
  width: 15px;
  height: 17px;
  font-size: 10px;
  color: #fff;
  position: absolute;
  border-radius: 50%;
  background-color: var(--main-color);
}
.navbar .dropdown-menu-heading {
  display: none;
}
@media (max-width: 992px) {
  .navbar .dropdown-menu-heading {
    display: flex;
    align-items: center;
    border-bottom: 0.0625rem solid rgba(0, 0, 0, 0.1);
    justify-content: space-between;
    height: 45px;
  }
}

@media (max-width: 991px) {
  .navbar {
    padding: 0;
  }
  .btn-close:focus {
    outline: 0;
    box-shadow: none;
  }
}
@keyframes moveUpDown {
  0%, 100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(30px);
  }
}
@keyframes moveLeftRight {
  0%, 100% {
    transform: translateX(0);
  }
  50% {
    transform: translateX(30px);
  }
}
@keyframes rotateScale {
  0%, 100% {
    transform: rotate(0deg) scale(1);
  }
  50% {
    transform: rotate(15deg) scale(1.1);
  }
}
@keyframes diagonalMove {
  0%, 100% {
    transform: translate(0, 0);
  }
  50% {
    transform: translate(20px, -20px);
  }
}
.hero-section {
  padding: clamp(24px, 5vw, 84px) 0;
}

.hero-text {
  text-transform: capitalize;
}
.hero-text h2 {
  font-size: clamp(32px, 5vw, 64px);
  font-weight: 700;
}
.hero-text h2 span:first-of-type {
  background-color: var(--main-color);
  color: white;
  padding: 0 8px;
}
.hero-text h2 #typed-text {
  font-weight: 300;
  display: block;
  height: clamp(46px, 8vw, 76px);
}
.hero-text p {
  margin: 24px 0;
  width: 75%;
  color: #555555;
  line-height: 1.75;
}
@media (max-width: 992px) {
  .hero-text h2 {
    font-size: 24px;
    font-weight: 500;
    line-height: 38px;
  }
  .hero-text p {
    font-size: 13px;
    margin: 14px 0;
    line-height: 24px;
    opacity: 0.65;
  }
}

.hero-image {
  position: relative;
  text-align: right;
  z-index: 1;
}
.hero-image img {
  height: clamp(330px, 50vw, 550px);
  right: 8%;
  bottom: 0;
  top: -50px;
}
.hero-image::before {
  position: absolute;
  content: "";
  width: clamp(320px, 40vw, 500px);
  height: clamp(320px, 40vw, 500px);
  background: linear-gradient(to bottom, rgba(255, 255, 255, 0.5), transparent);
  border-radius: 50%;
  right: 0;
  z-index: -1;
  top: 0;
}

.floating-img {
  position: absolute;
  pointer-events: none;
  transition: transform 0.1s linear;
  animation-duration: 5s;
  animation-iteration-count: infinite;
  animation-timing-function: ease-in-out;
}
.floating-img.book {
  left: 54%;
  width: 100px;
  bottom: 80px;
  animation-name: rotateScale;
}
.floating-img.circle {
  top: -20px;
  z-index: -1;
  opacity: 0.6;
  left: -20px;
}
.floating-img.red-plus {
  top: 40%;
  left: 52%;
  animation-name: moveLeftRight;
}
.floating-img.crown {
  top: 50%;
  right: 140px;
  z-index: 1;
  animation-name: diagonalMove;
}
.floating-img.dots {
  top: 152px;
  right: 12%;
  z-index: -1;
  animation-name: moveUpDown;
}
@media (max-width: 992px) {
  .floating-img.dots {
    top: 50%;
    right: 30px;
    z-index: 1;
  }
  .floating-img.book, .floating-img.crown {
    display: none;
  }
  .floating-img.red-plus {
    top: 60%;
    left: 4%;
    z-index: 1;
  }
  .floating-img.circle {
    top: -20px;
    width: 45%;
    left: -20px;
  }
}

